/* Smartphones (portrait and landscape) ----------- */
@media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
/* Styles */
	.formselect{
		display: block;
	 }
	.additionalDivAttributes, .container-fluid, .nodeelement, .control-label, .formcontainer {
		margin-left: 0px;
		margin-right: 0px;
		padding-left: 4px;
		padding-right: 4px;
	}
	.intro{
		padding-top:0px;
	}
	.control-label{
		width: 100%;
	}
}

